@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  overflow-x: hidden !important;
}
body {
  font-family: 'Roboto', Arial, sans-serif, Helvetica;
  line-height: 1.4;
  color: #333;
  background-color: rgb(247, 247, 247);
  overflow-x: hidden;
  font-family: 'Roboto';
}

/* utility classes */
.container {
  max-width: 1100px;
  margin: auto;
}

.text-primary {
  color: #3693ff;
}
.big-light {
  background: #f4f4f4f4;
  color: #fff;
}
.center {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.admin-dashbord {
  background-color: #fff;
  width: 350px;
  height: auto;
  margin: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
}
.admin-dashbord ul {
  margin: 20px;
}

.admin-dashbord img {
  width: 90px;
  height: 90px;
  border-radius: 100%;
  margin-left: 35px;
  margin-top: 10px;
}
.admin-dashbord img:hover {
  cursor: pointer;
}

.admin-dashbord h4 {
  position: absolute;
  margin: 20px;
  margin-left: 150px;
}
.admin-dashbord h4 strong {
  font-size: 15px;
  color: #fcc102;
  padding: 3px;
}
.admin-dashbord h4 span {
  font-size: 15px;
  font-weight: 900;
  color: #053e74;
}
.active-link {
  color: #fff;
  background-color: #053e74;
}
.admin-dashbord ul li {
  background-color: #444;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
  margin: 20px 10px;
  padding: 6px;
  border-bottom: 4px solid rgb(161, 128, 128, 0.6);
  border-bottom-left-radius: 60px;
  text-align: center;
}
.admin-dashbord ul li:hover {
  background-color: #625b9c;
  transition: 1s;
  color: rgb(19, 226, 140);
  cursor: pointer;
}
.admin-dashbord ul li a {
  font-size: 18px;
  color: rgb(19, 226, 140);
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande',
    'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-weight: 900;
}
.admin-dashbord ul li a:hover {
  color: rgb(19, 226, 140);
  cursor: pointer;
}
.admin-dashbord h4 {
  font-size: 23px;
}

.admin-dashbord ul li a:active {
  background-color: #444;
  color: rgb(19, 226, 140);
}

/* active POST */
.ditails {
  background-color: #fff;
  position: absolute;
  top: 0;
  right: 20px;

  width: 70%;
  margin: 20px 10px 10px;
  box-shadow: 0 0 3px rgba(9, 49, 29, 0.3);
}

.active {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  box-shadow: 0 0 3px rgba(9, 49, 29, 0.3);
  padding: 10px;
  padding: 10px;
}
.active select {
  background-color: #fff;
  border: 1px solid darkgreen;
  outline: none;
  border-radius: 5px;
  font-size: 24px;
  margin-left: 10px;
  padding: 5px 15px 5px 15px;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
}
.active select option {
  font-size: 34px;
}
/* .active h3 {
  position: absolute;
  margin: 10px;
  color: darkgreen;
  font-weight: 900;
} */
.active h3 span {
  font-size: 20px;
  color: #444;
}

.active select:hover {
  cursor: pointer;
}
.post-active option {
  font-size: 30px;
}

.chart {
  width: 450px;
  /* height: 300px; */
  display: flex;
  background-color: #fff;
  position: absolute;
  /* padding: 30px; */
  top: 90px;
  left: 28%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  box-shadow: 0 1px 4px black;
  border-radius: 10px;
}
.chart:hover {
  padding: 2px;
  cursor: move;
}
.chart button {
  padding: 10px;
  width: 30%;
  margin: 0 auto 10px;
  background-color: darkgreen;
  border: none;
  outline: none;
  color: #fff;
  border-radius: 5px;
}
.chart button h6 {
  font-size: 18px;
  font-style: normal;
  font-style: initial;
  font-weight: 900;
}
.chart button:hover {
  background-color: #444;
  color: #fff;
  cursor: pointer;
}

.report-par-post {
  width: 450px;
  /* height: 300px; */
  display: flex;
  background-color: #444;
  position: absolute;
  /* padding: 30px; */
  top: 90px;
  left: 63%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  box-shadow: 0 1px 4px black;
  border-radius: 10px;
}
.report-par-post:hover {
  padding: 2px;
  cursor: move;
}
.report-par-post button {
  padding: 10px;
  width: 40%;
  margin: 0 auto 10px;
  background-color: darkgreen;
  border: none;
  outline: none;
  color: #fff;
  border-radius: 5px;
}
.report-par-post button h6 {
  font-size: 14px;
  font-style: normal;
  font-style: initial;
  font-weight: 900;
}
.report-par-post button:hover {
  background-color: #fff;
  color: darkgreen;
  cursor: pointer;
}

table {
  background-color: rgb(107, 103, 103);
  width: 70%;
  margin-top: 30px;
  font-size: 25px;

  border-collapse: collapse;
  color: #fff;
  box-shadow: 0 0 3px rgba(9, 49, 29, 0.3);
  margin: 20px;
  margin-left: 28%;
  margin-top: -10%;
}
thead,
th,
tbody,
td {
  text-align: center;
  border: 3px solid rgba(19, 226, 140, 0.5);
  padding: 5px;
}

thead {
  color: #fcc206;
  font-size: 30px;
  font-weight: 900;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
td:hover {
  background-color: darkgreen;
  cursor: pointer;
  padding: 1px;
  border: 1px solid white;
}

.number {
  color: rgb(19, 226, 140);
  background-color: #000;
  font-weight: 900;
  border-bottom: 7px solid wheat;
  /* border-top: 4px solid #000; */
}
thead {
  background-color: #000;
}

.admin {
  background-color: rgb(247, 247, 247);
  position: relative;
  min-height: 900px;
  height: 90vh;
}

.main-header {
  width: 100%;
  height: 6rem;
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background: #fff;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);
  padding: 0 1rem;
  z-index: 999;
}

main {
  margin-top: 6rem;
}

@media (min-width: 768px) {
  .main-header {
    justify-content: space-between;
  }
}

.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.logo {
  width: 85px;
  height: 85px;
  position: absolute;
  top: 10px;
  left: 20px;

  background-color: none;
}
.image-profile {
  width: 40px;
  height: 40px;
}
.nav-links li {
  margin: 1rem;
}

.color-active {
  border: 1px solid transparent;
  color: #292929;
  text-decoration: none;
  padding: 0 1rem;
  margin: 3px;
}

.color-active:hover,
.color-active:active,
.color-active.active {
  background-color: #444;
  color: #fff;
  box-shadow: 0 1px 3px rgba(0, 0, 00.6);
  border-radius: 4px;
}

.nav-links button {
  cursor: pointer;
  border: 1px solid #292929;
  color: #292929;
  background: transparent;
  padding: 0.5rem;
  font: inherit;
}

.nav-links button:focus {
  outline: none;
}

.nav-links button:hover,
.nav-links button:active {
  background: darkgreen;
  color: #fff;
}

@media (min-width: 768px) {
  .nav-links {
    flex-direction: row;
  }

  .nav-links li {
    margin: 0 0.5rem;
  }

  .nav-links a {
    color: darkgreen;
    text-decoration: none;
  }

  .nav-links button {
    border: 1px solid white;
    color: white;
    background: transparent;
  }

  .nav-links button:hover,
  .nav-links button:active {
    background: darkgreen;
    color: #fff;
  }
  .nav-links img {
    visibility: visible;
  }
}

.main-navigation__menu-btn {
  width: 3rem;
  height: 3rem;
  background: transparent;
  border: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-right: 2rem;
  cursor: pointer;
}

.main-navigation__menu-btn span {
  display: block;
  width: 3.5rem;
  height: 1px;
  background: darkgreen;
}

.main-navigation__title {
  color: white;
}

.main-navigation__title a {
  text-decoration: none;
  color: #444;
  visibility: visible;
}

.main-navigation__header-nav {
  display: none;
}

.main-navigation__drawer-nav {
  height: 100%;
}

@media (min-width: 768px) {
  .main-navigation__menu-btn {
    display: none;
  }

  .main-navigation__header-nav {
    display: block;
  }
  .main-navigation__title a {
    text-decoration: none;
    color: #444;
    visibility: hidden;
  }
}

.navbar {
  background-color: #fff;
  text-align: center;
  display: flex;
  border-bottom: 2px solid #444;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1;
}
.navbar h1 {
  font-size: 25px;
  margin-top: 30px;
  font-weight: 900;
}
.navbar h1 a {
  text-decoration: none;
  color: #444;
}
.navbar h1 span {
  font-size: 40px;

  margin-left: -7px;
  color: #fcc102;
}
.navbar ul {
  margin-left: 200px;
}

.navbar ul li {
  display: inline-block;
  list-style: none;
  margin: 30px 30px;
}
.navbar ul li a {
  text-decoration: none;
  font-size: 23px;
  color: #444;
  font-weight: 900;
}

.navbar ul li a:hover,
.navbar ul li a.active {
  color: darkgreen;
  border-bottom: 2px solid #444;
}
.navbar img.image-profile {
  width: 50px;
  height: 50px;
  margin-top: 18px;
  margin-left: 130px;
}
.navbar img.logo {
  width: 100px;
  height: 100px;
