}
.navbar img.logo:hover {
  cursor: pointer;
}
/* .switch:hover {
  border-bottom: 3px solid green;
  font-size: 10px;
} */
.navbar ul li a:active,
.navbar ul li a.active {
  background-color: darkgreen;
  color: white;
}

footer {
  width: 100%;
  min-height: 600px;
  background-color: rgb(158, 155, 155, 0.5);
  background-color: rgb(4, 12, 4);
  z-index: 0;
}

footer > div {
  display: inline-block;
  margin: 20px 40px;
  position: relative;
}
footer > div.about {
  width: 400px;
  height: 300px;
  text-align: center;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.5);
  border: 1px solid #fff;
  border-radius: 50px;
  font-family: 'Roboto';
  font-size: 18px;
  color: #fff;
}

footer > div.about h3 {
  margin: 15px 0;
}

footer > div.about h3::after {
  content: '';
  display: block;
  margin: 0 auto;
  width: 50%;
  border-bottom: 4px solid #fff;
  margin-top: 4px;
}
footer > div.about:hover {
  transition: 0.6s;
  cursor: pointer;
}

footer > div.about button {
  font-size: 15px;
  padding: 10px;
  background-color: darkgreen;
  border: none;
  color: white;
  font-weight: bold;
  border-radius: 20px;
  margin: 10px 0 0;
  outline: none;
  cursor: pointer;
}

footer > div.about button:hover {
  background-color: #fff;
  color: darkgreen;
  transition: 0.5s;
  padding: 11px;
  width: 40%;
  font-size: 20px;
  border: 2px solid green;
  border-radius: 30px;
}

.link {
  position: absolute;
  width: 400px;
  height: 200px;
  border-radius: 5px;
  text-align: left;
  border: none;
  background: rgba(49, 158, 113, 0.6);
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.5);
  padding: 10px;
}
.link h3 {
  font-size: 15px;
  margin: 10px;
  font-weight: bold;
  color: #fff;
}
.link h3::after {
  content: '';
  display: block;
  margin: 3px 4px 3px;
  width: 20%;
  border-bottom: 2px solid #fff;
}

.link h5 a {
  color: #fff;
  display: inline-block;
  font-size: 15px;
  padding: 5px 10px;
  margin: 0 10px;
  text-decoration: none;
  list-style: none;
  margin-left: 35px;
}
.link h4,
h3,
h5:hover {
  cursor: pointer;
}
.link h5 a:hover {
  background-color: #fff;
  color: darkgreen;
  border-radius: 20px;
}

.social {
